.contentContact{
    margin-top: 3rem;
    width: 100%;
    display: flex;
    justify-content: center;
}

.wonder{
    margin-top: 1rem;
    height: auto;
    width: 47rem;    
    display: flex;
    justify-content: center;
    border-radius: 1rem;
    box-shadow: 0 1rem 2rem 0 rgba(0, 0, 0, 0.15);
}

.imgContact {
    height: 3rem;
    width: 3rem;
    border-radius: 1.3rem;
    margin-right: .5rem;
}

.infoContact {
    display: flex;
    justify-content: center;
    font-size: 1.2rem;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.imgFooter {
    width: 100%;
    height: 65%;
    border-bottom-color: white;
    margin-bottom: -5rem;
    opacity: .6;
    padding: 2rem;
  }